$white: #fdfcf8;
$cream: #eeefe1;
$blueBlack: #07161b;
// $greenBlack: #344e41;
$brown: #9c6644;
$border: 0.1rem solid $blueBlack;
$blur: rgba(0, 0, 0, 0.8);
$box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.1);
$brown2: #c79373;

@import url("https://fonts.googleapis.com/css2?family=Philosopher:ital@1&display=swap");
$font-family: "Philosopher", sans-serif;

@import url("https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,300;0,400;0,500;0,600;1,100;1,300&display=swap");
$font-family2: "Poppins", sans-serif;

.compoWrap {
  // font-family: $font-family;
  margin: 3rem 8rem;
  color: var(--clr);
  display: flex;
  flex-direction: column;
  gap: 2rem;

  .comFieldset {
    border-top: 0.1rem solid $brown;
    .heading {
      font-family: $font-family;
      font-size: 3rem;
      font-weight: 700;
      display: block;
      color: $brown;
      padding-right: 2rem;
    }
  }
}
